import React, {useState} from "react";
import Plus from "../../assets/icons/plus.png";
import Remove from "../../assets/icons/remove.png";

import {
  Nav,
  NavbarContainer,
  NavLogoItem,
  NavLogoContainer,
  NavSlide,
  NavItem,
  NavLinks,
  NavMobilePlus,
  NavMobileMinus,
  LogoItem,
  MobileItem,
} from "./NavbarElements";

const Navbar = ({
  mobile,
  setMobile,
  elements,
  setElements,
  setSlideIndex,
  swiper,
}) => {

  const [signal, setSignal] = useState(0);
  return (
    <>
      <Nav>
        <NavbarContainer>
          <NavLogoContainer>
            <NavLogoItem
              id="aboutLink"
              mobile={mobile}
              page={elements.about}
              onClick={() => {
                let index = 0;
                if (index === elements.index) {
                  index = 1;
                }
                setElements({
                  swiper: !elements.swiper,
                  about: !elements.about,
                  index: index,
                });
                setMobile(!mobile);
                if (elements.swiper) {
                  setSlideIndex(swiper.activeIndex);
                }
              }}
            >
              Miguel Lopes
            </NavLogoItem>
            <LogoItem mobile={mobile} page={elements.about}>
              Designer
            </LogoItem>

            <MobileItem page={elements.about}>
              <NavMobileMinus
                src={Remove}
                alt="Remove"
                mobile={mobile}
                onClick={() => {
                  setSignal(2)
                  setMobile(true);
                  let index = 0;
                if (index === elements.index) {
                  index = 1;
                }
                setElements({
                  swiper: !elements.swiper,
                  about: !elements.about,
                  index: index,
                });
                }}
                // onAnimationEnd={() => {setSignal(0)}}
              />
{/* <CSSTransition
              key={elements.id}
              timeout={{ enter: 500, exit: 500 }}
              className="sinal"
            > */}
              <NavMobilePlus
                className={signal===1 ? "rotateIn": signal===2?"rotateOut":""}
                src={Plus}
                alt="Plus"
                mobile={mobile}
                onClick={() => {
                  setSignal(1);
                  let index = 0;
                if (index === elements.index) {
                  index = 1;
                }
                setElements({
                  swiper: !elements.swiper,
                  about: !elements.about,
                  index: index,
                });
                  setSlideIndex(swiper.activeIndex);
                }}
                onAnimationEnd={() => { signal===2 ? setMobile(true): setMobile(false); setSignal(0); }}
              />
               {/* </CSSTransition> */}
            </MobileItem>
          </NavLogoContainer>

          <NavSlide mobile={mobile}>
            <NavItem page={elements.about}>Product/Graphic</NavItem>
            <NavLinks
              page={elements.about}
              href="mailto:miguel@miguellopes.info"
            >
              miguel@miguellopes.info
            </NavLinks>
          </NavSlide>
        </NavbarContainer>
      </Nav>
    </>
  );
};

export default Navbar;
